<template>
  <section>
    <el-breadcrumb separator-class="el-icon-arrow-right" style="height:30px;font-size:14px;">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="TableList" style="min-height: 550px; max-height: 650px; overflow: auto; padding:20px;background:#fff">
      <el-table ref="multipleTable" :data="infos" style="width:100%" border :height="total?550:100"  v-loading="loading" >
          <el-table-column type="index" label="" width="60" :index="index" align="center"></el-table-column>
          <el-table-column prop="custom_name" label="" align="center"></el-table-column>  
          <el-table-column prop="src_info" label="" align="center"></el-table-column>
          <el-table-column prop="custom_type" label="" align="center">
            <template slot-scope="scope">
                <span v-if="scope.row.custom_type==1"></span>
                <span v-else-if="scope.row.custom_type==2"></span>
            </template>
          </el-table-column>
          <el-table-column prop="address" label="" align="center"></el-table-column> 
          <el-table-column prop="industry_type" label="" align="center"></el-table-column> 
          <el-table-column prop="price" label="" align="center"></el-table-column> 
          <el-table-column prop="total_price" label="" align="center"></el-table-column> 
          <el-table-column prop="certificate_url" label="" align="center">
            <template slot-scope="scope">
              <a :href="scope.row.certificate_url" download style="color:#409EFF" v-show="scope.row.certificate_url"></a>
            </template>
          </el-table-column>
          <el-table-column prop="is_payoff" label="" align="center"></el-table-column> 
          <el-table-column prop="line" label="" align="center"></el-table-column>
          <el-table-column prop="contact" label="" align="center">
          </el-table-column>
          <el-table-column prop="sales_name" label=""align="center"></el-table-column> 
          <el-table-column prop="after_sale" label="" align="center"></el-table-column>
          <el-table-column prop="use_period" label="" align="center"></el-table-column>
          <el-table-column prop="signed_time" label="" align="center"></el-table-column>
      </el-table>
      <div class="pagination" v-show="!!total" style="margin:0 auto;width:300px;">
        <div class="block"> 
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[10, 15, 20, 25]"
                :page-size="page_size"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import {axiosRequest} from '@/assets/js/Yt.js'
  export default {
		data() {
			return {
        currentPage:1,
        page:1,
        page_size:10,
        total:"",
        infos:[],
        loading:true,
			}
		},
    beforeMount() {
      this.init()
    },
		methods: {
      index(val){
        return (this.page-1)*this.page_size+val+1
      },
      init(){
        this.loading = true
        const url = "/api_backend.php?r=customer-device/custom-list"
        const conf = {
          url,
          data:{
            page:this.page,
            page_size:this.page_size
          },
          success:(data)=>{
            this.loading = false
            this.infos = data.info.info
            this.total = parseInt(data.info.total_count)
          }
        }
        axiosRequest(conf)
      },
      handleSizeChange(val){
        this.page_size = val
        this.init()
      },
      handleCurrentChange(val){
        this.page = val
        this.init()
      }
	  }
	}
</script>


